
.custom-toggler {
  .navbar-toggler-icon {
    background-image:
      url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgb(25,26,29)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");

    @media #{$brk-narrow} {
      // $white?
    }

    }
}

/* Shows */

.filter {
  -webkit-filter: grayscale(30%), opacity(0.8); /* Safari 6.0 - 9.0 */
  filter: grayscale(30%) opacity(0.9);
  &:hover {
    -webkit-filter: grayscale(30%), opacity(0.8); /* Safari 6.0 - 9.0 */
    filter: grayscale(20%) opacity(0.8);
    transition: 0.2s;
    }
}

.filter:hover {
  -webkit-filter: grayscale(30%), opacity(0.8); /* Safari 6.0 - 9.0 */
  filter: grayscale(20%) opacity(0.8);
  transition: 0.2s;
}

// Chevron

.chevron-down {
  fill: $white;
  width: 4em;
  height: 4em;
  animation-duration: 0.5s;
  animation-name: hover-me;
  animation-iteration-count: infinite;
  animation-direction: alternate;

  @media #{$brk-narrow} {
    display: none;
  }
}

@keyframes hover-me {
   100% { transform: translate3d(0, 15px, 0); }
}

.slick-arrow {
  @media #{$brk-narrow} {
    display: none !important;
  }
}